body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  overflow-x: hidden; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}

.Navigation {
  display: flex;
  width: 100%;
  height: 80px;
  background-color: #111;
  justify-content: center;
  list-style: none; }

.Navigation--fixed {
  position: fixed;
  top: 0;
  height: 40px;
  z-index: 10; }
  .Navigation--fixed .NavigationItem {
    line-height: 40px; }

.NavigationItem {
  display: inline-block;
  color: #fff;
  font-size: 18px;
  padding: 0 18px;
  line-height: 80px;
  text-decoration: none;
  text-transform: uppercase; }
  .NavigationItem--active {
    color: #1BA79C; }
  .NavigationItem:hover {
    color: #1BA79C; }

.AboutIntro {
  width: 60%;
  margin: 0 auto;
  font-size: 20px; }

.AboutImage {
  padding: 30px 0; }

.AboutImage__photo {
  width: 230px; }

.About__Sub-heading {
  color: #fff;
  font-size: 80px;
  text-align: center;
  padding-bottom: 20px;
  text-transform: uppercase; }

.About__Toolbox {
  display: flex;
  flex-flow: row wrap;
  width: 70%;
  margin: 0 auto;
  justify-content: center;
  list-style-type: none;
  text-align: center;
  padding: 60px 0 30px 0; }

.About__Toolbox-icon {
  color: #fff;
  font-size: 100px;
  transition: all .3s ease-out;
  margin: 10px 20px; }
  .About__Toolbox-icon:hover {
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px); }
  @media screen and (max-width: 650px) {
    .About__Toolbox-icon {
      font-size: 100px; } }
  @media screen and (max-width: 540px) {
    .About__Toolbox-icon {
      font-size: 90px; } }

@media screen and (max-width: 700px) {
  .html5 {
    color: #E44D26; } }

.html5:hover {
  color: #E44D26; }

@media screen and (max-width: 700px) {
  .css3, .css3-alt {
    color: #167DBE; } }

.css3:hover, .css3-alt:hover {
  color: #167DBE; }

@media screen and (max-width: 700px) {
  .js {
    color: #F7E018; } }

.js:hover {
  color: #F7E018; }

@media screen and (max-width: 700px) {
  .react {
    color: #00BDFF; } }

.react:hover {
  color: #00BDFF; }

@media screen and (max-width: 700px) {
  .node-js, .nodejs {
    color: #78B743; } }

.node-js:hover, .nodejs:hover {
  color: #78B743; }

@media screen and (max-width: 700px) {
  .npm {
    color: #C53635; } }

.npm:hover {
  color: #C53635; }

@media screen and (max-width: 700px) {
  .github, .github-square {
    color: #2AA9DA; } }

.github:hover, .github-square:hover {
  color: #2AA9DA; }

.About {
  display: flex; }
  @media screen and (max-width: 1000px) {
    .About {
      flex-direction: column; } }

.About__intro {
  border-top: 2px solid #111;
  width: 50%;
  padding: 60px 0; }
  @media screen and (max-width: 1000px) {
    .About__intro {
      width: 100%;
      margin: 0 auto; } }

.About__toolbox {
  border-top: 2px solid #fff;
  width: 50%;
  background-color: #111;
  padding: 60px 0; }
  @media screen and (max-width: 1000px) {
    .About__toolbox {
      width: 100%;
      margin: 0 auto; } }

.About__area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0; }

.About__heading {
  font-size: 80px;
  text-align: center;
  text-transform: uppercase; }

.Portfolio {
  padding: 60px 0;
  max-width: 1040px;
  margin: 0 auto; }

.Portfolio__heading {
  font-size: 70px;
  text-align: center;
  text-transform: uppercase; }

.Portfolio__projects {
  display: flex;
  flex-flow: row wrap;
  padding: 30px 0; }

.Portfolio__card {
  color: #fff;
  width: 33.333%;
  height: 250px;
  background-position: center;
  background-size: cover;
  position: relative; }
  .Portfolio__card--background-position-top {
    background-position: top; }
  .Portfolio__card .Portfolio__hover-layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all .3s ease-out; }
  .Portfolio__card:hover .Portfolio__hover-layer {
    background-color: #2e3d4f; }
  .Portfolio__card:hover .Portfolio__subtitle,
  .Portfolio__card:hover .Portfolio__icon-box,
  .Portfolio__card:hover .Portfolio__link {
    opacity: 1; }
  @media screen and (max-width: 950px) {
    .Portfolio__card {
      width: 50%; } }
  @media screen and (max-width: 850px) {
    .Portfolio__card {
      width: 70%;
      margin: 0 auto; } }
  @media screen and (max-width: 600px) {
    .Portfolio__card {
      width: 80%; } }

.Portfolio__subtitle {
  font-size: 25px;
  position: absolute;
  top: 15%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0; }

.Portfolio__icon-box {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0; }

.Portfolio__icon {
  font-size: 40px;
  transition: all .3s ease-out; }
  .Portfolio__icon:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px); }
  .Portfolio__icon:not(:last-child) {
    margin-right: 10px; }

.Portfolio__link {
  background-color: #1BA79C;
  border-radius: 4px;
  color: #fff;
  display: inline-block;
  padding: 10px;
  font-size: 20px;
  position: absolute;
  top: 70%;
  left: 50%;
  width: 150px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  transition: all .3s ease-out;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  opacity: 0; }
  .Portfolio__link:hover {
    -webkit-transform: translate(-50%, -5px);
            transform: translate(-50%, -5px); }

.Contact {
  padding: 60px 0;
  text-align: center;
  max-width: 1040px;
  margin: 0 auto; }

.Contact__icon {
  font-size: 70px; }

.Contact__icon--success,
.Contact__subtitle--success {
  color: #0fa10f; }

.Contact__icon--fail,
.Contact__subtitle--fail {
  color: #d80202; }

.Contact__heading {
  font-size: 70px;
  text-transform: uppercase;
  margin-bottom: 30px; }

.Contact__subtitle {
  width: 70%;
  margin: 0 auto;
  font-size: 20px;
  font-weight: normal;
  text-align: center;
  padding-top: 30px; }

.Contact__form {
  width: 60%;
  margin: 0 auto;
  padding: 30px 0; }
  @media screen and (max-width: 750px) {
    .Contact__form {
      width: 70%; } }

.Contact__input {
  display: block;
  width: 100%;
  padding: 10px;
  margin: 20px 0; }

.Contact__button {
  border: none;
  border-radius: 5px;
  color: #fff;
  background-color: #3999D7;
  font-size: 16px;
  float: right;
  padding: 10px 20px;
  outline: none;
  transition: all .2s ease-out; }
  .Contact__button:hover {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.438);
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px); }
  .Contact__button:active {
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.438);
    -webkit-transform: translateY(0);
            transform: translateY(0); }

.Footer {
  background-color: #1B242F;
  color: #fff;
  text-align: center; }

.Footer__social-media {
  padding: 30px 0; }

.Footer__copyright {
  padding: 0 0 20px 0; }

.Media-icon {
  color: #fff;
  font-size: 30px; }
  .Media-icon a {
    color: currentColor; }
  .Media-icon .github:hover {
    color: #2AA9DA; }

.SocialMedia {
  padding: 10px 0; }

.App {
  margin: 0 auto; }

